import { css } from 'emotion';

export const buttonCss = css({
	label: `buttonCss`,
	display: 'inline-block',
	border: `1px solid rgba(0, 0, 0, .2)`,
	borderRadius: 3,
	background: 'white',
	padding: 5,
	cursor: 'pointer',
	
	':hover': {
		background: '#eee',
	},
	
	':active': {
		background: '#ddd',
	},
	
	':focus': {
		borderColor: 'teal',
		outline: 'none',
	},
})


